import React from 'react';
import { CheckIcon } from 'src/assets/icons';
import './bankItem.scss';
import { Grid } from '@material-ui/core';

function BankItem(props) {
  return (
    <Grid item lg={2} xl={6} md={6} sm={12} xs={12}>
      <div className="bank-container-item">
        {props.isSelected ? <CheckIcon className="icon" /> : null}
        <button
          type="button"
          onClick={props.onClick}
          className={props.isSelected ? 'selected_button' : 'button'}
        >
          <img alt="" src={props.image} className="bank_image" />
        </button>
      </div>
    </Grid>
  );
}

export default BankItem;
