import React from 'react';
import Grid from '@material-ui/core/Grid';
import PaymentItem from 'src/components/base/layout/PaymentItem';
import Button from '@material-ui/core/Button';
import { PayPointIcon, MoneyPaperIcon } from 'src/assets/icons';
import './paymentTypes.scss';
import { PAYMENT_TYPE } from 'src/constants';
import { convertCurrency } from 'src/utils/currency';
import useStyles from './styles';

const PaymentTypes = (props) => {
  const classes = useStyles();
  return (
    <Grid container className="payment-container">
      <Grid
        item
        xl={12}
        lg={12}
        md={12}
        sm={12}
        xs={10}
        className={classes.titleSection}
      >
        <div>
          <p className="payment-title">CHỌN PHƯƠNG THỨC THANH TOÁN</p>
        </div>
        <div className={classes.line} />
      </Grid>
      <Grid className="content-section">
        {props.dealType === 'Voucher' && (
          <Grid className="payment-method-container" item>
            <PaymentItem
              isSelected={props.paymentType === PAYMENT_TYPE.GATE_WAY}
              onClick={() => {
                props.setPaymentType(PAYMENT_TYPE.GATE_WAY);
              }}
              icon={
                props.paymentType === PAYMENT_TYPE.GATE_WAY ? (
                  <div style={{ height: 60 }}>
                    <PayPointIcon className="iconTransSVG" />
                  </div>
                ) : (
                  <div style={{ height: 60 }}>
                    <PayPointIcon />
                  </div>
                )
              }
              title="Thanh toán ngay"
              description="Qua cổng thanh toán"
            />
          </Grid>
        )}
        <Grid className="payment-method-container" item>
          <PaymentItem
            className
            isSelected={props.paymentType === PAYMENT_TYPE.POINT}
            onClick={() => {
              props.setPaymentType(PAYMENT_TYPE.POINT);
            }}
            icon={
              props.paymentType === PAYMENT_TYPE.POINT ? (
                <div style={{ height: 60 }}>
                  <PayPointIcon className="iconTransSVG" />
                </div>
              ) : (
                <div style={{ height: 60 }}>
                  <PayPointIcon />
                </div>
              )
            }
            title="Thanh toán điểm"
            description="Thanh toán bằng điểm thành viên"
          />
        </Grid>
        {props.dealType === 'Delivery' && (
          <Grid className="payment-method-container" item>
            <PaymentItem
              customStyle="iconMoneyDimesion"
              isSelected={props.paymentType === PAYMENT_TYPE.TRANSFER}
              onClick={() => {
                props.setPaymentType(PAYMENT_TYPE.TRANSFER);
              }}
              icon={
                props.paymentType === PAYMENT_TYPE.TRANSFER ? (
                  <MoneyPaperIcon className="iconMoneySVG" />
                ) : (
                  <MoneyPaperIcon />
                )
              }
              title="Thanh toán sau"
              description="Thanh toán khi nhận hàng"
            />
          </Grid>
        )}
        <Grid item className="infor-payment-container">
          <div className="infor-item-payment-container">
            <p className={`title-pay ${classes.inforText}`}>
              Thông tin thanh toán
            </p>
            <div className={classes.totalSection}>
              <p className={`sum ${classes.totalText}`}>Tổng tiền</p>
              <p className={`sum-money ${classes.price}`}>
                {convertCurrency(props.money)} VNĐ
              </p>
            </div>
          </div>
        </Grid>
      </Grid>
      <Grid
        item
        xl={12}
        lg={12}
        md={12}
        sm={12}
        xs={12}
        className={classes.titleSection}
      >
        <Button
          className="button-confirm"
          onClick={props.onClick}
          disableElevation
          disabled={!props.paymentType}
          classes={{
            root: classes[props.paymentType ? 'selectBtn' : 'disableButton'],
          }}
        >
          <p className={classes.selectText}>Thanh Toán ngay</p>
        </Button>
      </Grid>
    </Grid>
  );
};

export default PaymentTypes;
