import React, { useState, useEffect } from 'react';
import Button from '@material-ui/core/Button';
import { IconBank } from 'src/assets/icons';
import './paymentFormPopup.scss';
import { Grid } from '@material-ui/core';
import { getBankInfoHandle } from 'src/redux/actions/common.action';
import { useDispatch } from 'react-redux';
import BankItem from './components/bankItem';
import useStyles from './styles';

function PaymentFormPopup() {
  const classes = useStyles();
  const [listBank, setListBank] = useState([]);
  const [selectedBank, setSelectedBank] = useState({});
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(
      getBankInfoHandle((bankInfo) => {
        const data = JSON.parse(bankInfo.data);
        setListBank(data.bankinfo);
        if (data) {
          setSelectedBank(data.bankinfo[0]);
        }
      })
    );
  }, []);
  return (
    <div className={`form-popup-container ${classes['form-popup-container']}`}>
      <p className={`header-title ${classes['header-title']}`}>
        Hình thức thanh toán
      </p>
      <div
        className={`directly-transfer-section ${classes['directly-transfer-section']}`}
      >
        <IconBank />
        <p
          className={`directly-transfer-text ${classes['directly-transfer-text']}`}
        >
          Chuyển khoản trực tiếp
        </p>
      </div>
      <p className={`select-bank-text ${classes['select-bank-text']}`}>
        Chọn ngân hàng
      </p>
      <Grid
        container
        className="bank-selection-container"
        style={{
          display: 'flex',
          flexDirection: 'row',
          marginTop: 22,
          paddingBottom: 32,
          borderBottom: '1px solid',
          borderBottomColor: '#DFE2E7',
        }}
        spacing={3}
      >
        {listBank.map((obj, idx) => {
          return (
            <BankItem
              image={obj.bankLogo}
              key={idx.toString()}
              isSelected={selectedBank.id === obj.id}
              onClick={() => {
                setSelectedBank(obj);
              }}
            />
          );
        })}
      </Grid>
      <p className={`bank-transfer-text ${classes['bank-transfer-text']}`}>
        Chuyển khoản ngân hàng
      </p>
      <div className={classes['detail-section']}>
        <Grid container spacing={3}>
          <Grid item xl={6} lg={6} xs={12} sm={12} md={12}>
            <div className={classes['detail-left-section']}>
              <p className={`bank-name ${classes['bank-name']}`}>
                {selectedBank?.name || ''}
              </p>
              <p className={`bank-name-detail ${classes['bank-name-detail']}`}>
                {selectedBank?.bankName || ''}
              </p>
              <Grid container className={classes['account-section']}>
                <Grid
                  lg={5}
                  md={4}
                  sm={5}
                  xs={5}
                  className={classes['account-title-section']}
                >
                  <div className={classes['flex-row']}>
                    <p
                      className={`account-title-text ${classes['account-title-text']}`}
                    >
                      Tên tài khoản:
                    </p>
                    {/* <p className={classes['account-detail-text']}>
                      {selectedBank?.accountName || ''}
                    </p> */}
                  </div>
                  <div className={classes['flex-row']}>
                    <p
                      className={`account-title-text ${classes['account-title-text']}`}
                    >
                      Số tài khoản:
                    </p>
                    {/* <p className={classes['account-detail-text']}>
                      {selectedBank?.accountNumber || ''}
                    </p> */}
                  </div>
                  <div className={classes['flex-row']}>
                    <p
                      className={`account-title-text ${classes['account-title-text']}`}
                    >
                      Chi nhánh:
                    </p>
                    {/* <p className={classes['account-detail-text']}>
                      {selectedBank?.branchName || ''}
                    </p> */}
                  </div>
                </Grid>
                <Grid
                  lg={7}
                  md={6}
                  sm={7}
                  xs={7}
                  className={`account-title-section ${classes['account-title-section']}`}
                >
                  <p
                    className={`account-detail-text ${classes['account-detail-text']}`}
                  >
                    {selectedBank?.accountName || ''}
                  </p>
                  <p
                    className={`account-detail-text ${classes['account-detail-text']}`}
                  >
                    {selectedBank?.accountNumber || ''}
                  </p>
                  <p
                    className={`account-detail-text ${classes['account-detail-text']}`}
                  >
                    {selectedBank?.branchName || ''}
                  </p>
                </Grid>
              </Grid>
              <Button
                classes={{
                  root: classes['button-custom'],
                }}
                className={`button-copy ${classes.button}`}
              >
                <p className={classes['button-text-account']}>
                  Sao chép số tài khoản
                </p>
              </Button>
            </div>
          </Grid>
          <Grid item xl={6} lg={6} xs={12} sm={12} md={12}>
            <div className={classes['detail-right-section']}>
              <p className={`note-title ${classes['note-title']}`}>
                Ghi chú giao dịch rõ ràng:
              </p>
              <br />
              <div className={classes['account-section']}>
                <div className={classes['account-title-section']}>
                  <p className={`register-text ${classes['register-text']}`}>
                    Dang ky (so dien thoai)
                  </p>
                  <p
                    className={`register-example ${classes['register-example']}`}
                  >
                    VD: Dang Ky
                  </p>
                  <p
                    className={`register-example ${classes['register-example']}`}
                  >
                    (09121323129)
                  </p>
                </div>
              </div>
              <Button
                classes={{
                  root: classes['button-custom'],
                }}
                className={`button-copy ${classes.button}`}
              >
                <p className={classes['button-text-transaction']}>
                  Sao chép giao dịch
                </p>
              </Button>
            </div>
          </Grid>
        </Grid>
      </div>
      <div>
        <p className={`note-pay ${classes['button-pay-text']}`}>
          Điểm sẽ được cập nhật ngay khi chúng tôi nhận được khoản thanh toán
        </p>
      </div>
    </div>
  );
}

export default PaymentFormPopup;
