/* eslint-disable import/prefer-default-export */
const convertCreditCardNumber = (cardNumber) => {
  const cardNumberInStringFormat = String(cardNumber);
  let tempCardNumber = String(cardNumber);
  if (!cardNumber) {
    return null;
  }
  let cardNumberConverted = String(cardNumber);
  tempCardNumber = cardNumberInStringFormat.replace(/\D/g, '');
  if (/^\d{0,17}$/.test(tempCardNumber)) {
    cardNumberConverted = tempCardNumber
      .replace(/(\d{5})/, '$1 ')
      .replace(/(\d{4}) (\d{4})/, '$1 $2 ')
      .replace(/(\d{4}) (\d{4}) (\d{4})/, '$1 $2 $3 ')
      .replace(/(\d{4}) (\d{4}) (\d{4}) (\d{4})/, '$1 $2 $3 $4');
  }
  return cardNumberConverted;
};

export { convertCreditCardNumber };
