import React from 'react';
import { CheckIcon } from 'src/assets/icons';
import useStyles from './styles';
import './paymentItem.scss';

function PaymentItem(props) {
  const classes = useStyles();
  return (
    <div className={classes.PaymentContainer}>
      <button
        type="button"
        onClick={props.onClick}
        className={`button-payment-container ${classes.button}`}
      >
        {props.isSelected ? (
          <div className={classes.icon}>
            <CheckIcon />
          </div>
        ) : null}
        <div className={`icon-svg ${props.customStyle}`}>{props.icon}</div>
        <div>
          <p className={`title-payment ${classes.firstLine}`}>{props.title}</p>
          <p className={`description-payment ${classes.secondLine}`}>
            {props.description}
          </p>
        </div>
      </button>
    </div>
  );
}

export default PaymentItem;
